.advantages {
  width: 100%;
  min-height: 864px;
  background-color: $color-default-white;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.advantages__container {
  max-width: 1920px;
  width: 100%;
  min-height: 100%;
  padding-top: 104px;
  padding-bottom: 98px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.advantages h2 {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.advantages ul {
  list-style-type: none;
  padding: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 30px;
  row-gap: 30px;
}

@media (max-width: $tablet-max-width) and (min-width: $tablet-min-width) {
  .advantages {
    min-height: 830px;
  }

  .advantages__container {
    width: 770px;
    padding: 64px 50px 34px;
  }

  .advantages ul {
    grid-template-columns: 320px 320px;
  }
}

@media (max-width: $mobile-width) {
  .advantages {
    min-height: 1420px;
  }

  .advantages__container {
    min-width: 320px;
    width: 100%;
    padding: 50px 25px 47px;
  }

  .advantages ul {
    grid-template-columns: 1fr;
    margin: 0;
  }
}
