.social {
  position: absolute;
  top: 119px;
  left: 360px;
}

.social ul {
  list-style-type: none;
  width: 150px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 15px;
  padding: 0;
}

.social li {
  width: 40px;
  height: 40px;
}

.social a {
  fill: $color-alice-blue;
  cursor: pointer;
  display: flex;
  width: 40px;
  height: 40px;
  align-items: center;
  justify-content: space-around;
}

.social a:hover {
  fill: $color-deep-sky-blue;
}

.social h3,
.social h4 {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

@media (max-width: $desktop-elastic-max-width) and (min-width: $desktop-elastic-min-width) {
  .social {
    left: 7%;
  }
}

@media (max-width: $tablet-max-width) and (min-width: $tablet-min-width) {
  .social {
    top: 39px;
    left: 575px;
  }
}

@media (max-width: $mobile-width) {
  .social {
    top: 88px;
    left: calc(50% - 73px);
  }
}
